router.beforeEach((to, from, next) => {
	const isLogin = sessionStorage.getItem("chang_yi_User_token");
	if((isLogin == null || isLogin == '') && to.path != '/login') {
		// Toast.fail("");
		next("/login");
		return;
	}
	next();
})
/*after next */
router.afterEach((to, from) => {

})
